'use client'

import Link from 'next/link'
import { useState } from 'react'
import Sidebar from '../Components/Sidebar'
export default function PincodePage() {
  const [captchaInput, setCaptchaInput] = useState('')
  
  const statesAndUTs = [
    "Andhra Pradesh", "Arunachal Pradesh", "Assam", "Bihar", "Chhattisgarh",
    "Goa", "Gujarat", "Haryana", "Himachal Pradesh", "Jharkhand", "Karnataka",
    "Kerala", "Madhya Pradesh", "Maharashtra", "Manipur", "Meghalaya", "Mizoram",
    "Nagaland", "Odisha", "Punjab", "Rajasthan", "Sikkim", "Tamil Nadu",
    "Telangana", "Tripura", "Uttar Pradesh", "Uttarakhand", "West Bengal",
    "Andaman and Nicobar Islands", "Chandigarh", 
    "Dadra and Nagar Haveli and Daman and Diu", "Delhi", "Jammu and Kashmir",
    "Ladakh", "Lakshadweep", "Puducherry"
  ]

  const handleSubmit = (e) => {
    e.preventDefault()
  }

  const handleReset = () => {
    // Reset form
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Breadcrumb */}
      <div className="bg-white border-b">
        <div className="container mx-auto px-4 py-2">
          <p className="text-sm">
            You are here: <Link href="/" className="text-blue-600 hover:underline">Home</Link> {'>'} Find Nearest Post Office
          </p>
        </div>
      </div>

      <div className="container mx-auto px-4 py-6">
        <div className="grid grid-cols-1 md:grid-cols-[250px_1fr] gap-6">
          <Sidebar />
          <div className="bg-white p-6 shadow-lg">
            <h1 className="text-3xl font-bold mb-4">Find Nearest Post Office</h1>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="space-y-4">
                <p className="text-sm">* Indicates a required field.</p>
                
                <div>
                  <label className="block text-sm mb-1">
                    State / Union Territory <span className="text-red-500">*</span>
                  </label>
                  <select 
                    className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-red-800 focus:border-red-800"
                    required
                  >
                    <option value="">--Select--</option>
                    {statesAndUTs.map((state, index) => (
                      <option key={index} value={state}>{state}</option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm mb-1">
                    City / District <span className="text-red-500">*</span>
                  </label>
                  <select 
                    className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-red-800 focus:border-red-800"
                    required
                  >
                    <option value="">--Select--</option>
                  </select>
                </div>

                <div className="text-center font-medium">OR</div>

                <div>
                  <label className="block text-sm mb-1">
                    PinCode
                  </label>
                  <input 
                    type="text"
                    className="w-full p-2 border rounded focus:ring-2 focus:ring-red-800 focus:border-red-800"
                  />
                </div>
              </div>

              {/* Buttons */}
              <div className="flex gap-4 justify-end">
                <button
                  type="submit"
                  className="px-6 py-2 bg-red-800 text-white rounded hover:bg-red-900 focus:outline-none focus:ring-2 focus:ring-red-800 focus:ring-offset-2"
                >
                  Search
                </button>
                <button
                  type="button"
                  onClick={handleReset}
                  className="px-6 py-2 bg-gray-200 text-gray-800 rounded hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-gray-400 focus:ring-offset-2"
                >
                  Reset
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

